<template>
  <div class="app-index">
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <!-- 首页个人信息 beign -->
      <div class="index-userinfo lineGradient">
        <div class="index-userinfo-bg" />
        <div class="index-userinfo-content">
          <div class="userinfo-tx">
            <img :src="txurl">
          </div>
          <div class="userinfo-layout">
            <div class="userinfo-name">
              <span>{{ userInfo.name }}</span>
              <span class="tel">
                <i class="iconfont iconshouji" />
                {{ userInfo.sj }}
              </span>
            </div>
            <div class="userinfo-c">
              <span>
                <i class="iconfont iconbianma" />
                {{ userInfo.gh }}
              </span>
              <span>
                <i class="iconfont iconzuzhijigou" />
                {{ userInfo.zzmc }}
              </span>
              <div class="userinfo-erweima" />
            </div>
          </div>
        </div>
      </div>
      <!-- 首页个人信息 end -->

      <!-- 功能区 beign -->
      <div class="index-main">
        <div class="function-item box">

          <div class="function-list">
            <dl @click="gotoWddbList()">
              <dt>
                <i class="iconfont icondaiban" />
              </dt>
              <dd>我的待办({{ dbsl }})</dd>

            </dl>
            <dl @click="gotoWdybList()">
              <dt>
                <i class="iconfont iconhegezhipingshenlei" />
              </dt>
              <dd>我的已办({{ ybsl }})</dd>
            </dl>
          </div>
        </div>
      </div>
      <!-- 功能区 end -->

      <!-- 待办事项 begin -->
      <common-list
        v-show="showWddb"
        ref="dida_list"
        class="common-list"
        :post-url="wddbList.postUrl"
        :req-data="wddbList.reqData"
        :data-name="wddbList.dataName"
        @returnData="getPrizeLog($event)"
      >
        <div class="content">
          <div class="common-main box">
            <!-- 通用标题 beign -->
            <!--<div class="title">
            <div class="title-left">
              <h2>待办事项</h2>
            </div>
            <div class="title-right">
              <router-link to="/DbsxIndex"> 更多
              <i class="iconfont iconarrowright"></i>
              </router-link>
            </div>
          </div>-->
            <!-- 通用标题 end -->
            <div v-if="wddbPage.pageStyles == 0 " class="common-list">
              <div
                v-for="(item, index) in wddbList.items"
                :key="index"
                class="list"
                @click="onDbClick(index)"
              >
                <h3><span v-show="item.showTimeDay != 4" class="ti">{{ TimeDay[item.showTimeDay] }}</span>{{ item.processName }}</h3>
                <div class="info">
                  <span>
                    <i class="iconfont iconlianxiren" />
                    {{ item.preUsers }}
                  </span>
                  <span>
                    <i class="iconfont iconshijian" />
                    {{ item.showTime }}
                  </span>
                </div>
              </div>
            </div>
            <div v-else>
              <error-page
                :page-style="wddbPage.pageStyles"
                :page-title="wddbPage.pageTitle"
                @errorButton="getWddbList"
              />
            </div>
          </div>
        </div>
      </common-list>
      <!-- 待办事项 end -->

      <!-- 已办事项 begin -->
      <common-list
        v-show="showWdyb"
        ref="dida_list"
        class="common-list"
        :post-url="wdybList.postUrl"
        :req-data="wdybList.reqData"
        :data-name="wdybList.dataName"
        @returnData="getPrizeYbLog($event)"
      >
        <div class="content">
          <div class="common-main box">
            <div v-if="wdybPage.pageStyles == 0 " class="common-list">
              <div
                v-for="(item, index) in wdybList.items"
                :key="index"
                class="list"
                @click="onYbClick(index)"
              >
                <h3><span v-show="item.showTimeDay != 4" class="ti">{{ TimeDay[item.showTimeDay] }}</span>{{ item.processName }}</h3>
                <div class="info">
                  <span>
                    <i class="iconfont iconlianxiren" />
                    {{ item.preUsers }}
                  </span>
                  <span>
                    <i class="iconfont iconshijian" />
                    {{ item.showTime }}
                  </span>
                </div>
              </div>
            </div>
            <div v-else>
              <error-page
                :page-style="wdybPage.pageStyles"
                :page-title="wdybPage.pageTitle"
                @errorButton="getWdybList"
              />
            </div>
          </div>
        </div>
      </common-list>
      <!-- 已办事项 end -->

      <!-- 版本信息 begin -->
      <div class="mine-copyright">当前版本:{{ version }}</div>
      <!-- 版本信息 end -->
    </van-pull-refresh>
    <!--<FooterNav />-->
  </div>

</template>
<script land="script">
import { mapState } from 'vuex'
import constants from '../../../common/js/constants'
import commonList from '../../../components/list/commonList.vue'
import axios from 'axios'
import { Dialog } from 'vant' // axios http请求库
export default {
  FixedHeader: false,
  name: 'Jbrspindex',
  components: {
    commonList
  },
  data() {
    return {
      version: process.env.VUE_APP_VERSION,
      ind: null, // 点击事件
      pageStyle: true, // 页面状态
      isRefresh: false,
      scrollY: true,
      listdirection: 'up',
      isLoading: false,
      finished: true,
      loading: false,
      FixedHeader: false,
      dbsl: '',
      ybsl: '',
      pageno: 1, // 页
      totalpageno: 0, // 总页数
      showWddb: true,
      showWdyb: true,
      wddbList: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      wddbPage: {
        pageTitle: '',
        pageStyles: 0
      },
      wdybList: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      wdybPage: {
        pageTitle: '',
        pageStyles: 0
      },
      txurl: require('../../../assets/images/default-tx.png') // 头像地址
    }
  },
  computed: {
    // 取数据
    ...mapState({
      userInfo: state => state.mh.mhUserInfo
    })
  },
  created() {
    this.TimeDay = constants.TimeDay
    this.txurl = this.userInfo.tx_url
    this.getDbsl()
    this.getYbsl()
    this.getWdybList()
    this.getWddbList()
  },
  mounted: function() {
    this.$nextTick(function() {
      this.showWdyb = false
    })
  },
  methods: {
    onRefresh() {
      this.getAll()
    },
    onLoad() {
      this.getAll()
    },
    getAll() {
      axios.all([
        this.getWdybList(),
        this.getWddbList(),
        this.getDbsl(),
        this.getYbsl()
      ])
        .then(axios.spread(function() {
          console.info('function')
        }))
    },
    onDbClick(index) {
      this.$store.dispatch('jbrdbsx/setDbsxlistItem', this.wddbList.items[index])
      // this.SET_DBSXLIST_ITEM(this.wddbList.items[index])
      // if (this.wddbList.items[index].readTime !== null) {
      this.goDbxqPage(this.wddbList.items[index], '待办详情')
      // } else {
      //   this.getRead(this.wddbList.items[index])
      // }
    },
    onYbClick(index) {
      this.$store.dispatch('jbrdbsx/setDbsxlistItem', this.wdybList.items[index])
      // this.SET_DBSXLIST_ITEM(this.wddbList.items[index])
      // if (this.wddbList.items[index].readTime !== null) {
      this.goDbxqPage(this.wdybList.items[index], '已办详情')
      // } else {
      //   this.getRead(this.wddbList.items[index])
      // }
    },
    goDbxqPage(item, title) {
      this.$store
        .dispatch('jbrdbsx/updateTxZtAction', {
          id: item.id
        })
        .then(res => {
          this.loading = false
          let txfs = item.txfs
          if (item.ta && item.ta !== '') {
            txfs = item.ta
          }
          if (txfs === 3) { // 流程处理
            this.$store.dispatch('jbrdbsx/setTxbizid', item.bizId)
            this.$router.push({
              name: 'wddbxq',
              params: { title: title }
            })
          } else if (txfs === 2) { // 功能中处理
            Dialog.alert({
              title: item.processName,
              message: ((item.txnr === item.processName) ? '' : item.txnr) + '\n该任务需前往PC端进行办理！'
            }).then(() => {
            })
          } else { // 普通提醒
            Dialog.confirm({
              message: item.txnr,
              confirmButtonText: '已阅'
            }).then(() => {
              this.$store.dispatch('jbrdbsx/dealTxActions', {
                ywbh: item.bizId
              }).then(res => {
                setTimeout(() => {
                  this.onRefresh()
                }, 2000)
              }).catch(() => {
              })
            }).catch(() => {
              // on cancel
            })
          }
        })
        .catch(() => {
          // this.loading = false
        })
    },
    ItemClick() {
      this.$emit('ItemClick')
    },
    /** 组件中返回的数据结构
       */
    getPrizeLog(data) {
      console.log('=========data:=========' + data)
      this.wddbList.items = data
    },
    getPrizeYbLog(data) {
      console.log('=========wdybList data:=========' + data)
      this.wdybList.items = data
    },
    // getRead(item) {
    //   this.$store
    //     .dispatch('jbrdbsx/getdbRead', {
    //       id: item.bizId
    //     }).then(res => {
    //       this.goDbxqPage(item)
    //     }).catch(() => {
    //     })
    // },
    getDbsl() {
      this.$store
        .dispatch('jbrdbsx/getDbslAction', {
          userid: this.userInfo.id,
          apptype: process.env.VUE_APP_Type
        })
        .then(res => {
          this.loading = false
          this.dbsl = res
        })
        .catch(() => {
          // this.loading = false
        })
    },
    getYbsl() {
      this.$store
        .dispatch('jbrdbsx/getYbslAction', {
          userid: this.userInfo.id,
          apptype: process.env.VUE_APP_Type
        })
        .then(res => {
          this.loading = false
          this.ybsl = res
        })
        .catch(() => {
          // this.loading = false
        })
    },
    getWddbList() {
      this.wddbList.postUrl = 'jbrdbsx/getWddbListAction'
      this.wddbList.reqData = {
        pageno: this.pageno,
        userid: this.userInfo.id
      }
      this.wddbList.dataName = 'executors'
    },
    getWdybList() {
      this.wdybList.postUrl = 'jbrdbsx/getWdybListAction'
      this.wdybList.reqData = {
        pageno: this.pageno,
        userid: this.userInfo.id
      }
      this.wdybList.dataName = 'executors'
      /* this.$store
                .dispatch('jbrdbsx/getWdybListAction', {
                  pageno: this.pageno,
                  userid: this.userInfo.id
                })
                .then(res => {
                  this.loading = false
                  if (res.length === 0) {
                    this.wddbPage.pageStyles = 1
                    this.$toast('暂无数据！')
                  } else {
                    this.wddbList.items = res.executors
                  }
                  this.isLoading = false
                  this.loading = false
                })
                .catch(() => {
                  // this.loading = false
                })*/
    },
    gotoWddbList() {
      this.showWddb = true
      this.showWdyb = false
    },
    gotoWdybList() {
      this.showWddb = false
      this.showWdyb = true
    }
    /* getWdybList() {
        this.wdybList.postUrl = 'jbrdbsx/getWdybListAction'
        this.wdybList.reqData = { 'userid': this.userInfo.id }
        this.wdybList.dataName = 'executors'
      },
      gotoWddbList() {
        this.showWddb = true
        this.showWdyb = false
        this.$store
                .dispatch('jbrdbsx/getWddbListAction', {
                  userid: this.userInfo.id
                })
                .then(res => {
                  this.loading = false
                  if (res.length === 0) {
                    this.wddbPage.pageStyles = 1
                    this.$toast('暂无数据！')
                  } else {
                    this.wddbList.items = res.executors
                  }
                  this.isLoading = false
                  this.loading = false
                })
                .catch(() => {
                  // this.loading = false
                })
      },
      gotoWdybList() {
        this.showWddb = false
        this.showWdyb = true
        this.$store
                .dispatch('jbrdbsx/getWdybListAction', {
                  userid: this.userInfo.id
                })
                .then(res => {
                  this.loading = false
                  if (res.length === 0) {
                    this.wdybPage.pageStyles = 1
                    this.$toast('暂无数据！')
                  } else {
                    this.wdybList.items = res.executors
                  }
                  this.isLoading = false
                  this.loading = false
                  //this.ybsl = res.executors.length
                })
                .catch(() => {
                  // this.loading = false
                })
      },*/
  }
}
</script>

<style lang="css" scoped>
  .app-index{
    margin-bottom:50px;
  }
</style>

